import React from 'react';
import { ReactNode, Ref } from 'react';

const Section = React.forwardRef(
  (
    {
      title,
      actions,
      className,
      children,
      isOpaqueBg = false,
      isBorderLess = false,
    }: {
      title?: ReactNode;
      actions?: ReactNode;
      className?: string;
      children: ReactNode;
      isOpaqueBg?: boolean;
      isBorderLess?: boolean;
    },
    ref: Ref<HTMLElement>
  ) => {
    return (
      <section
        ref={ref}
        className={`my-5 rounded-md ${
          isOpaqueBg
            ? isBorderLess
              ? ''
              : 'rounded-lg border-[1px] border-gray-200 border-opacity-80 px-5 dark:border-gray-700'
            : 'bg-slate-50 px-5 dark:bg-slate-800'
        } dark:text-slate-300 ${className ?? ''}`}
      >
        {title || actions ? (
          <div
            className={`relative flex flex-row items-center border-b-[1px] border-gray-200 border-opacity-80 py-5 transition-all duration-300 dark:border-gray-700`}
          >
            <h3 className="text-2xl dark:text-white">{title}</h3>
            <div className="ml-auto">
              <div className="-m-2 flex flex-col flex-wrap justify-end sm:flex-row sm:items-center">
                {actions}
              </div>
            </div>
          </div>
        ) : null}
        <div className="py-5 ">{children}</div>
      </section>
    );
  }
);

export default Section;
