import { useQuery } from '@tanstack/react-query';
import { PagingOptions } from '@youfoundation/dotyoucore-js';
import { CircleNetworkProvider } from '../../provider/connections/CircleNetworkProvider';
import useAuth from '../auth/useAuth';

interface useActiveConnectionsProps {
  active: { pageSize: number; pageNumber: number };
}

const useConnections = (
  { active: { pageSize: activePageSize, pageNumber: activePage } }: useActiveConnectionsProps = {
    active: { pageSize: 10, pageNumber: 1 },
  }
) => {
  const { getSharedSecret } = useAuth();
  const circleNetworkProvider = CircleNetworkProvider.getInstance(getSharedSecret());

  const fetchConnections = async (
    { pageSize, pageNumber }: PagingOptions = { pageSize: 10, pageNumber: 1 }
  ) => {
    return await await circleNetworkProvider.getConnections({
      pageNumber: pageNumber,
      pageSize: pageSize,
    });
  };

  return {
    fetchActive: useQuery(
      ['activeConnections', activePageSize, activePage],
      () => fetchConnections({ pageSize: activePageSize, pageNumber: activePage }),
      {
        refetchOnWindowFocus: false,
        keepPreviousData: true,
        onError: (err) => console.error(err),
      }
    ),
  };
};

export default useConnections;
