import useContact from '../../../hooks/contacts/useContact';
import LoadingParagraph from '../../ui/Loaders/LoadingParagraph/LoadingParagraph';
import PersonCard, { PersonCardProps } from '../PersonCard/PersonCard';

const ConnectionCard = (props: PersonCardProps) => {
  const { data: contactData, isLoading } = useContact({
    dotYouId: props.dotYouId,
  }).fetch;
  const nameData = contactData?.name;
  const fullName = nameData
    ? `${nameData.givenName ?? ''} ${nameData.surname ?? ''}`
    : props.dotYouId;

  if (isLoading) {
    return <LoadingParagraph className={`aspect-[3/5] ${props.className}`} />;
  }

  return (
    <PersonCard {...props}>
      <h2 className="font-thiner mb-6 dark:text-white">{fullName ?? props.dotYouId}</h2>
      {props.children}
    </PersonCard>
  );
};

export default ConnectionCard;
