import { useEffect, useState } from 'react';
import { createPortal } from 'react-dom';
import { t } from '../../../helpers/i18n/dictionary';
import useCircle from '../../../hooks/circles/useCircle';
import useDrives from '../../../hooks/drives/useDrives';
import usePortal from '../../../hooks/portal/usePortal';
import { CircleDefinition } from '../../../provider/connections/CircleDataTypes';
import {
  circlePermissionLevels,
  drivePermissionLevels,
} from '../../../provider/permission/permissionLevels';
import ErrorNotification from '../../ui/Alerts/ErrorNotification/ErrorNotification';
import ActionButton from '../../ui/Buttons/ActionButton';
import Input from '../../Form/Input';
import Label from '../../Form/Label';
import PermissionFlagsEditor from '../../Form/PermissionFlagsEditor';
import PermissionLevelEditor from '../../Form/PermissionLevelEditor';
import Textarea from '../../Form/Textarea';
import HardDrive from '../../ui/Icons/HardDrive/HardDrive';
import DialogWrapper from '../../ui/Dialog/DialogWrapper';

const CircleDialog = ({
  title,
  confirmText,
  isOpen,
  defaultValue,
  onConfirm,
  onCancel,
}: {
  title: string;
  confirmText?: string;

  isOpen: boolean;
  defaultValue?: CircleDefinition;

  onConfirm: () => void;
  onCancel: () => void;
}) => {
  const target = usePortal('modal-container');
  const {
    mutateAsync: createOrUpdate,
    status: createOrUpdateStatus,
    error: updateError,
  } = useCircle({}).createOrUpdate;
  const { data: drives } = useDrives().fetch;

  const [newCircleDefinition, setNewCircleDefinition] = useState<CircleDefinition>({
    driveGrants: defaultValue.driveGrants || [],
    ...defaultValue,
  });

  useEffect(() => {
    if (isOpen) {
      setNewCircleDefinition({
        driveGrants: defaultValue.driveGrants || [],
        ...defaultValue,
      });
    }
  }, [isOpen]);

  if (!isOpen) {
    return null;
  }

  const reset = () => {
    setNewCircleDefinition({
      driveGrants: defaultValue.driveGrants || [],
      ...defaultValue,
    });
  };

  const dialog = (
    <DialogWrapper title={title} onClose={onCancel} size="4xlarge">
      <>
        <ErrorNotification error={updateError} />
        <form
          onSubmit={async (e) => {
            e.preventDefault();

            await createOrUpdate(newCircleDefinition);
            reset();
            onConfirm();

            return false;
          }}
        >
          <div className="mb-5">
            <Label htmlFor="name">{t('Name')}</Label>
            <Input
              id="name"
              name="circleName"
              defaultValue={newCircleDefinition.name}
              onChange={(e) => {
                setNewCircleDefinition({ ...newCircleDefinition, name: e.target.value });
              }}
              required
            />
          </div>
          <div className="mb-5">
            <Label htmlFor="name">{t('Description')}</Label>
            <Textarea
              id="description"
              name="circleDescription"
              defaultValue={newCircleDefinition.description}
              onChange={(e) => {
                setNewCircleDefinition({
                  ...newCircleDefinition,
                  description: e.target.value,
                });
              }}
              required
            />
          </div>

          <div className="mb-5 flex flex-row">
            <Label htmlFor="name" className="my-auto mr-2">
              {t('Permissions')}
            </Label>
            <PermissionFlagsEditor
              className="ml-auto"
              permissionLevels={circlePermissionLevels}
              defaultValue={newCircleDefinition.permissions.keys ?? [0]}
              onChange={(newValue) => {
                setNewCircleDefinition({
                  ...newCircleDefinition,
                  permissions: { keys: newValue },
                });
              }}
            />
          </div>

          {drives?.length ? (
            <div className="my-7">
              <div className="mb-8">
                <h2 className="text-lg">{t('Drive Permissions')}:</h2>
                <p className="text-sm text-slate-500">
                  {t('Drives with anonymous access enabled are available to any user')}
                </p>
              </div>

              <div className="-my-4">
                {drives
                  .filter((drive) => !drive.ownerOnly)
                  .map((drive, index) => {
                    const defaultVal =
                      newCircleDefinition.driveGrants?.find(
                        (driveGrant) =>
                          driveGrant.permissionedDrive.drive.alias ===
                            drive.targetDriveInfo.alias &&
                          driveGrant.permissionedDrive.drive.type === drive.targetDriveInfo.type
                      )?.permissionedDrive.permission ?? 0;

                    return (
                      <div
                        key={index}
                        className={`my-4 flex w-full select-none flex-row rounded-lg border p-4 dark:border-slate-800 ${
                          defaultVal > 0 && 'bg-slate-50 dark:bg-slate-700'
                        }`}
                      >
                        <HardDrive className="my-auto mr-3 h-6 w-6" />
                        <Label
                          htmlFor={`${drive.targetDriveInfo.alias}-${drive.targetDriveInfo.type}`}
                          className="mt-auto mb-auto mr-2"
                        >
                          {drive.name}
                          <small className="block text-sm text-slate-500">
                            {drive.allowAnonymousReads && t('Allows anonymous access')}
                          </small>
                        </Label>
                        <PermissionLevelEditor
                          className="my-auto ml-auto"
                          permissionLevels={drivePermissionLevels}
                          defaultValue={
                            newCircleDefinition.driveGrants?.find(
                              (driveGrant) =>
                                driveGrant.permissionedDrive.drive.alias ===
                                  drive.targetDriveInfo.alias &&
                                driveGrant.permissionedDrive.drive.type ===
                                  drive.targetDriveInfo.type
                            )?.permissionedDrive.permission ?? 0
                          }
                          onChange={(value) => {
                            if (value > 0) {
                              setNewCircleDefinition({
                                ...newCircleDefinition,
                                driveGrants: [
                                  ...(newCircleDefinition?.driveGrants?.filter(
                                    (driveGrant) =>
                                      driveGrant.permissionedDrive?.drive.alias !==
                                        drive.targetDriveInfo.alias ||
                                      driveGrant.permissionedDrive?.drive.type !==
                                        drive.targetDriveInfo.type
                                  ) || []),
                                  {
                                    permissionedDrive: {
                                      drive: drive.targetDriveInfo,
                                      permission: value,
                                    },
                                  },
                                ],
                              });
                            } else {
                              setNewCircleDefinition({
                                ...newCircleDefinition,
                                driveGrants: [
                                  ...newCircleDefinition.driveGrants.filter(
                                    (driveGrant) =>
                                      driveGrant.permissionedDrive.drive.alias !==
                                        drive.targetDriveInfo.alias ||
                                      driveGrant.permissionedDrive.drive.type !==
                                        drive.targetDriveInfo.type
                                  ),
                                ],
                              });
                            }
                          }}
                        />
                      </div>
                    );
                  })}
              </div>
            </div>
          ) : null}

          <div className="-m-2 flex flex-row-reverse py-3">
            <ActionButton className="m-2" state={createOrUpdateStatus} icon={'send'}>
              {confirmText || t('Add Circle')}
            </ActionButton>
            <ActionButton
              className="m-2"
              type="secondary"
              onClick={() => {
                reset();
                onCancel();
              }}
            >
              {t('Cancel')}
            </ActionButton>
          </div>
        </form>
      </>
    </DialogWrapper>
  );

  return createPortal(dialog, target);
};

export default CircleDialog;
